.Uptopi {
  -webkit-animation: Uptopi 0.5s cubic-bezier(0.1, 0.82, 0.25, 1);
          animation: Uptopi 0.5s cubic-bezier(0.1, 0.82, 0.25, 1);
}
@-webkit-keyframes Uptopi {
0%, 30% {
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
    opacity: 0;
}
100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    opacity: 1;
}
}
@keyframes Uptopi {
0%, 30% {
    -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
    opacity: 0;
}
100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    opacity: 1;
}
}
